import { RouterModule, Routes } from '@angular/router';
import { MenuComponent } from './components/menu/menu.component';
import { AppComponent } from './app.component';
import { InfoempresaComponent } from './components/infoempresa/infoempresa.component';
import { InfocontactoComponent } from './components/infocontacto/infocontacto.component';
import { ArticuloComponent } from './components/articulo/articulo.component';
import { HomeComponent } from './components/home/home.component';

const APP_ROUTES: Routes = [
    //Manejo de rutas para navegaci√≥n
    {path: 'main', component: AppComponent},
    {path: 'appMenu', component: MenuComponent},
    {path: 'appInfoEmpresa', component: InfoempresaComponent},
    {path: 'appInfoContacto', component: InfocontactoComponent},
    {path: 'articulo', component: ArticuloComponent},
    {path: 'home', component: HomeComponent},

    { path: '**', pathMatch: 'full', redirectTo: 'home'}
];
export const APP_ROUTING = RouterModule.forRoot(APP_ROUTES, { useHash: true });