import { Component, Input, Output, Directive, OnInit } from '@angular/core';
import { InfocontactoComponent } from './components/infocontacto/infocontacto.component';
import { ServicioService } from './service/servicio.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers: [ServicioService]
})

export class AppComponent implements OnInit{
 
  info:any[] = [];
  sidebar:boolean = false;
  constructor(private _service:ServicioService){
  console.log("constructor");

  }
 ngOnInit(){
   this.info = this._service.getInfo();
   this.sidebar = this._service.getSidebar();
 }
  
 openSideBar(){
  this.sidebar = true;
}

closeSideBar(){
  this.sidebar = false;
 
}
}

